<template>
  <el-cascader
    :options="address"
    :props="props"
    placeholder="选择地址"
    @change="handleChange"
    style="width: 100%"
  >
  </el-cascader>
</template>

<script>
import address from "./address.js";
export default {
  props: {
    /* 返回省市区代码还是中文，默认为代码 */
    code: {
      type: Boolean,
      default: true,
    },
  },
  name: "LybSelectAddress",
  data() {
    return {
      props: {
        value: "value",
        label: "text",
        children: "children",
      },
    };
  },
  created() {
    this.props.value = this.code ? "value" : "text";
  },
  computed: {
    address() {
      return address;
    },
  },
  methods: {
    handleChange(val) {
      this.$emit("select-change", val);
    },
  },
};
</script>
